
.index-container{background:#d9e0e7
}
@media (max-width: 767px){
.index-container .header-wap{display:block
}
.index-container .header-pc{display:none
}
.index-container .wap-header{border-bottom:1px solid #ededed
}
}
@media (min-width: 768px){
.index-container .header-wap{display:none
}
.index-container .header-pc{display:block
}
}
.index-container .header{height:55px;line-height:55px;text-align:center;z-index:11;background:#fff
}
.index-container .header .logo{text-align:left;padding-left:28px
}
.index-container .header .logo>*{display:inline-block;vertical-align:middle
}
.index-container .header .logo img{width:30px;height:30px
}
.index-container .header .logo h1{font-size:16px;font-weight:normal
}
.header-right{text-align:right
}
.header-right>*{text-align:center;display:inline-block
}
.header-right>*>em{display:inline-block;vertical-align:middle
}
.header-right .feedback{cursor:pointer;margin:0 5px 0 15px
}
.feedback-dropdown{top:35px !important;width:120px
}
.header-right .nickname{cursor:pointer;margin-right:30px
}
.header-right .nickname img{width:30px;height:30px;border-radius:50%;vertical-align:middle;margin-right:2px
}
.nickname-dropdown{top:35px !important;width:160px
}
@media (max-width: 767px){
.index-container .content{margin-top:55px
}
}
.index-container .content .left-menu-container{position:fixed;width:220px;z-index:10;top:56px;bottom:0;overflow:hidden;overflow-y:auto;transition:all 300ms ease;-webkit-transform:translateX(0);transform:translateX(0)
}
.index-container .content .left-menu-container .el-menu-item,.index-container .content .left-menu-container .el-submenu__title{height:40px;line-height:40px;font-size:14px
}
@media (max-width: 767px){
.index-container .content .left-menu-container{-webkit-transform:translateX(-100%);transform:translateX(-100%);top:0px;z-index:12
}
}
.index-container .content .left-menu-container>div.nav-title{font-size:14px;color:#999;margin:12px 20px 0
}
.index-container .content .left-menu-container.isShowMenue{-webkit-transform:translateX(0);transform:translateX(0)
}
.index-container .content .index-right-content{position:absolute;padding:75px 20px 20px 240px;width:100%;background:#d9e0e7;min-height:100%;height:100%;top:0;overflow:auto
}
@media (max-width: 767px){
.index-container .content .index-right-content{padding:75px 20px 20px;position:absolute
}
}
.index-container .content .index-right-content .back-btn{cursor:pointer;color:#58B7FF
}
.index-container .content .index-right-content .back-btn:hover{text-decoration:underline
}
.index-container .content .index-right-content .right-title{font-size:24px;margin-bottom:15px
}
.index-container .content .index-right-content .right-title>small{font-weight:300;font-size:70%;display:inline-block
}



