
.login-container{position:fixed;top:0;left:0;width:100%;height:100%;background:#666 url("./images/bg.jpg") no-repeat;background-size:cover
}
.login-container .login-mask{position:absolute;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.4)
}
.login-container .login-mask .content{margin-top:2rem
}
.login-container .login-mask .content .login-header{position:relative;width:90%;margin-bottom:80px
}
.login-container .login-mask .content .login-header img{width:28px;height:28px;margin-right:5%
}
.login-container .login-mask .content .login-header *{color:#fff;font-weight:300
}
.login-container .login-mask .content .login-header h1{font-size:22px
}
.login-container .login-mask .content .login-header h1 *{display:inline-block;vertical-align:top
}
.login-container .login-mask .content .login-header p{margin-top:5px;font-size:14px
}
.login-container .login-mask .content .login-header .icon{position:absolute;right:0;top:0;opacity:.4;filter:alpha(opacity=40)
}
.login-container .login-mask .content .login-header .icon>i{font-size:50px;line-height:52px;color:#ccc
}
.login-container .login-mask .content .form{border-radius:5px;padding:30px 0;background:rgba(0,0,0,0.4);width:100%;display:block;margin:0 auto !important;text-align:center
}
.login-container .login-mask .content .form>*{display:block;margin:0 auto;width:80%;margin-top:.3rem
}
.login-container .login-mask .content .form>*:first-of-type{margin-top:0
}
.login-container .login-mask .content .form input{background:rgba(0,0,0,0.4);color:#fff;border:none
}
.login-container .login-mask .content .form input:focus{box-shadow:none
}
.login-container .login-mask .content .form ::-webkit-input-placeholder{color:#999
}
.login-container .login-mask .content .form .submit{width:100%
}
@media (max-width: 767px){
.login-container .login-mask .content{margin-top:20px
}
.login-container .login-mask .content .login-header>div{width:50%
}
.login-container .login-mask .content .form{padding:1.6rem 0
}
.login-container .login-mask .content .form>*{margin-top:1.2rem
}
}



